function sprintf(str) {
  for (let i = 1; i < arguments.length; i += 1) {
    str = str ? str.replace(/%s/, arguments[i]) : '';
  }
  return str;
}

// Check for invalid filters
function getInvalidFilter(allowedFilters = [], reqFilters = {}) {
  let invalidFilterName = null;
  Object.keys(reqFilters).forEach((filterName) => {
    if (!allowedFilters.includes(filterName)) {
      invalidFilterName = filterName;
    }
  });
  return invalidFilterName;
}

// Check for required filters
function getRequiedFilter(requiredFilters = [], reqFilters = {}) {
  const invalidFilters = [];
  const reqFilterKeys = Object.keys(reqFilters);
  requiredFilters.forEach((filterName) => {
    if (!reqFilterKeys.includes(filterName)) {
      invalidFilters.push(filterName);
    }
  });
  return invalidFilters;
}

function validateURL(str) {
  const pattern = new RegExp('^(https?:\\/\\/)?' // protocol
    + '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|' // domain name
    + '((\\d{1,3}\\.){3}\\d{1,3}))' // OR ip (v4) address
    + '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' // port and path
    + '(\\?[;&a-z\\d%_.~+=-]*)?' // query string
    + '(\\#[-a-z\\d_]*)?$', 'i'); // fragment locator
  return !!pattern.test(str);
}

export const Utils = {
  sprintf,
  getInvalidFilter,
  getRequiedFilter,
  validateURL,
};
