import { BaseService } from './base-service';
import { constants } from '../constant';
import { errors } from '../error';
import { promiseOrCallback } from '../utils/promise';
import { Logger } from '../utils/logger';

const { INFO, WARN, ERROR } = constants.LOG_LEVELS;

export class FileService extends BaseService {
  static basePath = constants.BASEPATH_FILES;

  /**
  * Upload a File
   *
   * @method upload
   * @param {Object} file File Object - fileElement.files[0]
   * @param {String} type [image, video, audio]
   * @param {Boolean} thumbnail
   * @param {Function} cb
   * @return {Object}
  */
  static upload(file, type, thumbnail, cb) {
    if (!FileService.Client || !FileService.Client.getInstance()) {
      return cb('You have not initialized channelize client yet.');
    }
    const client = FileService.Client.getInstance();

    return promiseOrCallback(cb, (cb) => {
      if (!(file instanceof File)) {
        Logger.log(ERROR, errors.INVALID_FILE_OBJECT, {});
        return cb(errors.INVALID_FILE_OBJECT);
      }

      FileService.__getMetaData(file, type, thumbnail, (err, metaData) => {
        if (err) return cb(err);

        const requestUploadUrlData = FileService.__getRequestUploadUrlData(metaData);
        FileService.__requestUploadUrl(requestUploadUrlData, (err, fileData) => {
          if (err) return cb(err);

          const uploadPromises = [];
          uploadPromises.push(client.request(fileData.uploadUrl, {
            method: 'put',
            data: file,
            absolute: true,
            noAuth: true,
            headers: {
              'Content-Type': file.type,
            },
          }));

          if (fileData.uploadThumbnailUrl) {
            const { thumbnailFile } = metaData;
            uploadPromises.push(client.request(fileData.uploadThumbnailUrl, {
              method: 'put',
              data: thumbnailFile,
              absolute: true,
              noAuth: true,
              headers: {
                'Content-Type': thumbnailFile.type,
              },
            }));
          }

          Promise.all(uploadPromises).then((res) => {
            metaData.fileUrl = fileData.fileUrl;
            metaData.thumbnailUrl = fileData.thumbnailUrl;
            metaData.type = metaData.attachmentType;
            return cb(null, metaData);
          }).catch((err) => {
            Logger.log(ERROR, errors.API_ERROR, { err });
            return cb(err, null);
          });
        });
      });
    });
  }

  // return data uri containing a representation of the image
  static __dataURLtoBlob(dataurl) {
    const arr = dataurl.split(','); const mime = arr[0].match(/:(.*?);/)[1];
    const bstr = atob(arr[1]); let n = bstr.length; const
      u8arr = new Uint8Array(n);
    while (n -= 1) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new Blob([u8arr], { type: mime });
  }

  // Create thumbnail for image or video
  static __getMetaData(file, type, thumbnail, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!FileService.Client || !FileService.Client.getInstance()) {
        return cb('You have not initialized channelize client yet.');
      }
      const client = FileService.Client.getInstance();

      const metaData = {
        name: file.name,
        mimeType: file.type,
        extension: file.name.split('.').pop(),
        attachmentType: type,
      };

      if (!thumbnail || !client.browser) {
        return cb(null, metaData);
      }

      // PARENT ELEMENT
      const parentElement = document.body;
      if (type === 'audio' || type === 'video') {
        const urlobject = URL.createObjectURL(file);
        const videoElement = document.createElement('video');
        videoElement.setAttribute('style', 'visibility: hidden; display: none; position: absolute;');
        videoElement.autoplay = true;
        videoElement.controls = true;

        const sourceElement = document.createElement('source');
        sourceElement.setAttribute('src', urlobject);
        videoElement.appendChild(sourceElement);
        parentElement.appendChild(videoElement);

        let canvasElement;
        let canvasCtx;
        if (type === 'video') {
          canvasElement = document.createElement('canvas');
          canvasCtx = canvasElement.getContext('2d');
        }

        videoElement.addEventListener('play', () => {
          if (
            typeof window !== 'undefined'
          && window != null
          && window.setTimeout != null
          ) {
            window.setTimeout(() => {
              const duration = videoElement.duration * 1000;
              metaData.duration = duration;

              if (type === 'video') {
                canvasElement.width = 180;
                canvasElement.height = 180;
                canvasCtx.drawImage(videoElement, 0, 0, 180, 180);
                metaData.thumbnailFile = FileService.__dataURLtoBlob(canvasElement.toDataURL());
              }

              videoElement.parentNode.removeChild(videoElement);
              return cb(null, metaData);
            }, 2000);
          } else {
            return cb(null, metaData);
          }
        });
      } else {
        const urlobject = URL.createObjectURL(file);
        const imgElement = document.createElement('img');
        imgElement.setAttribute('src', urlobject);
        imgElement.setAttribute('style', 'visibility: hidden; display: none; position: absolute;');
        parentElement.appendChild(imgElement);

        imgElement.onload = () => {
          const canvasElement = document.createElement('canvas');
          const canvasCtx = canvasElement.getContext('2d');

          canvasElement.width = 180;
          canvasElement.height = 180;
          canvasCtx.drawImage(imgElement, 0, 0, 180, 180);
          metaData.thumbnailFile = FileService.__dataURLtoBlob(canvasElement.toDataURL());
          parentElement.removeChild(imgElement);
          return cb(null, metaData);
        };
      }
    });
  }

  // Return params required for requesting upload url
  static __getRequestUploadUrlData(metaData) {
    if (!FileService.Client || !FileService.Client.getInstance()) {
      throw new Error('You have not initialized channelize client yet.');
    }
    const client = FileService.Client.getInstance();

    const filename = metaData.name;
    const data = { name: filename };
    if (metaData.thumbnailFile) {
      const thumbnailName = filename.substring(0, filename.lastIndexOf('.'));
      data.thumbnail = {
        name: `${thumbnailName}-thumbnail.png`,
      };
    }
    return data;
  }

  // request upload url for file and thumbnail
  static __requestUploadUrl(body, cb) {
    if (!FileService.Client || !FileService.Client.getInstance()) {
      return cb('You have not initialized channelize client yet.');
    }
    const client = FileService.Client.getInstance();

    return promiseOrCallback(cb, (cb) => {
      const headers = {
        'Content-Type': 'application/json',
      };
      body = JSON.stringify(body);
      client.request(FileService.basePath, { method: 'post', data: body, headers }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: client.error(err) });
        return cb(client.error(err));
      });
    });
  }
}
