import { UserModel } from '../../models/user-model';
import { ConversationModel } from '../../models/conversation-model';
import { MessageModel } from '../../models/message-model';

export class ResponseTransformService {
  /* Response Processing Functions
  Only those functions has been added where response needs to be changed. */
  /* Function name will be same as eventName
  Function will return array of arguments */

  /* Dummy Function - return [args] for returning same payload
  static eventName(topic, args) {
    return [args];
  }
  */

  /* Response Processing - friendAdded Event */
  static friendAdded(topic, args) {
    const user = new UserModel(args);
    return [user];
  }

  /* Response Processing - userBlocked Event */
  static userBlocked(topic, args) {
    const isSelf = (topic.split('/').pop() === 'blocked');
    const userId = isSelf ? args.requesterId : args.blockedUserId;
    return [isSelf, userId];
  }

  /* Response Processing - userUnblocked Event */
  static userUnblocked(topic, args) {
    const isSelf = (topic.split('/').pop() === 'unblocked');
    const user = new UserModel(args);
    user.id = args.userId;
    return [isSelf, user];
  }

  /* Response Processing - online Event */
  static online(topic, args) {
    args.id = args.userId;
    const user = new UserModel(args);
    return [user];
  }

  /* Response Processing - offline Event */
  static offline(topic, args) {
    args.id = args.userId;
    const user = new UserModel(args);
    return [user];
  }

  /* Response Processing - messageReceived Event */
  static messageReceived(topic, args) {
    const message = new MessageModel(args);
    return [message];
  }
}
