/* eslint no-console: ["error", { allow: ["warn", "log", "error"] }] */
import { constants } from '../constant';

const PRECEDENCE = constants.LOG_LEVEL_PRECEDENCE;
export class Logger {
  static level = constants.LOG_LEVELS.WARN;

  static setLogLevel = (level) => {
    Logger.level = level;
  }

  static canLog = (level) => PRECEDENCE[level] >= PRECEDENCE[Logger.level];

  static setLogger = (logger) => {
    Logger.logger = logger;
  }

  static logger = (level, message, params) => {
    const prepend = Logger.getPrependText();
    if (level === constants.LOG_LEVELS.INFO) {
      console.log(`${prepend} [INFO]: ${message}`, params);
    } else if (level === constants.LOG_LEVELS.WARN) {
      console.warn(`${prepend} [WARN]: ${message}`, params);
    } else if (level === constants.LOG_LEVELS.ERROR) {
      console.error(`${prepend} [ERROR]: ${message}`, params);
    }
  }

  static log(level, message, params) {
    if (Logger.canLog(level)) {
      Logger.logger(level, message, params);
    }
  }

  static getPrependText() {
    const date = new Date();
    const timestamp = `${date.toLocaleTimeString()}:${date.getMilliseconds()}`;
    return `${timestamp} Channelize`;
  }
}
