export class BaseModel {
  // Add properties in model
  constructor(client, data) {
    this._client = client;
    const __data = {};
    const properties = this.constructor.__properties || {};
    Object.keys(properties).forEach((property) => {
      __data[property] = Object.prototype.hasOwnProperty.call(data, property)
        ? data[property]
        : properties[property];
    });

    Object.keys(properties).forEach((property) => {
      Object.defineProperty(this, property, {
        get() {
          return __data[property];
        },
        set(value) {
          __data[property] = value;
        },
      });
    });
  }

  // Return properties of model
  toJSON() {
    const properties = this.constructor.__properties || {};
    const json = {};
    Object.keys(properties).forEach((key) => {
      json[key] = this[key];
    });
    return json;
  }
}
