import { BaseModel } from './base-model';
import { constants } from '../constant';
import { errors } from '../error';
import { Utils } from '../utils';
import { promiseOrCallback } from '../utils/promise';
import { Logger } from '../utils/logger';

const { INFO, WARN, ERROR } = constants.LOG_LEVELS;

export class UserModel extends BaseModel {
  static __properties = {
    id: null,
    createdAt: null,
    displayName: null,
    email: null,
    isOnline: null,
    language: null,
    lastSeen: null,
    notification: null,
    profileImageUrl: null,
    profileUrl: null,
    updatedAt: null,
    visibility: null,
    isDeleted: null,
    isActive: null,
    role: null,
    metaData: null,
    accessToken: null,
  };

  static basePath = constants.BASEPATH_USERS;

  /**
   * update User Information
   *
   * @method updateInfo
   * @param {Object} data
   * @param {Function} cb
   * @return {Object} UserModel
  */
  updateInfo(data, cb) {
    return promiseOrCallback(cb, (cb) => {
      // Check Params Validation
      if (typeof data !== 'object') {
        Logger.log(ERROR, errors.INVALID_DATA, {});
        return cb(errors.INVALID_DATA);
      }

      const allowedFilters = ['displayName', 'isOnline', 'language', 'notification', 'profileImageUrl', 'profileUrl', 'visibility'];
      const invalidFilterName = Utils.getInvalidFilter(allowedFilters, data);
      if (invalidFilterName) {
        Logger.log(ERROR, Utils.sprintf(errors.INVALID_DATA_PARAM, invalidFilterName), {});
        return cb(Utils.sprintf(errors.INVALID_DATA_PARAM, invalidFilterName));
      }

      // Request Channelize API
      this._client.request(`${UserModel.basePath}/${this.id}`, { method: 'put', data }).then((res) => cb(null, new UserModel(this._client, res.data))).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Update User Photo
   *
   * @method updateProfilePhoto
   * @param {Object} data
   * @param {Function} cb
   * @return {Object} UserModel
  */
  updateProfilePhoto(img, cb) {
    return promiseOrCallback(cb, (cb) => {
      // Check Params Validation
      if (typeof img !== 'object') {
        Logger.log(ERROR, errors.INVALID_PROFILE_IMG, {});
        return cb(errors.INVALID_PROFILE_IMG);
      }

      const thumbnail = false;
      this._client.File.upload(img, 'image', thumbnail, (err, res) => {
        if (err) return cb(err);

        const data = {
          profileImageUrl: res.fileUrl,
        };

        this._client.request(`${UserModel.basePath}/${this.id}`, { method: 'put', data }).then((res) => cb(null, new UserModel(this._client, res.data))).catch((err) => {
          Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
          return cb(this._client.error(err));
        });
      });
    });
  }
}
