import { BaseQuery } from './base-query';
import { ConversationModel } from '../models/conversation-model';
import { errors } from '../error';
import { constants } from '../constant';
import { promiseOrCallback } from '../utils/promise';
import { Logger } from '../utils/logger';

const { INFO, WARN, ERROR } = constants.LOG_LEVELS;

export class ConversationListQuery extends BaseQuery {
  static basePath = constants.BASEPATH_CONVERSATIONS;

  constructor(client) {
    super(client);
    this._filter = {};
  }

  get ids() {
    return this._filter.ids;
  }

  set ids(values) {
    this._filter.ids = values;
  }

  get type() {
    return this._filter.type;
  }

  set type(type) {
    this._filter.type = type;
  }

  get customTypes() {
    return this._filter.customTypes;
  }

  set customTypes(value) {
    this._filter.customTypes = value;
  }

  get search() {
    return this._filter.search;
  }

  set search(values) {
    this._filter.search = values;
  }

  get isGroup() {
    return this._filter.isGroup;
  }

  set isGroup(value) {
    this._filter.isGroup = value;
  }

  get membersExactly() {
    return this._filter.membersExactly;
  }

  set membersExactly(values) {
    this._filter.membersExactly = values;
  }

  get membersIncluded() {
    return this._filter.membersIncluded;
  }

  set membersIncluded(values) {
    this._filter.membersIncluded = values;
  }

  get includeDeleted() {
    return this._filter.includeDeleted;
  }

  set includeDeleted(value) {
    this._filter.includeDeleted = value;
  }

  get includeOnlyActive() {
    return this._filter.includeOnlyActive;
  }

  set includeOnlyActive(value) {
    this._filter.includeOnlyActive = value;
  }

  get publicMode() {
    return this._filter.publicMode;
  }

  set publicMode(value) {
    this._filter.publicMode = value;
  }

  get sort() {
    return this._filter.sort;
  }

  set sort(field) {
    this._filter.sort = field;
  }

  get include() {
    return this._filter.include;
  }

  set include(value) {
    const allowedFiltersValues = ['members'];
    if (typeof value !== 'string' && !allowedFiltersValues.includes(value)) {
      throw new Error(errors.INVALID_CONVERSATION_INCLUDE);
    }

    let includeArray = value.split(',');
    includeArray = includeArray.map((s) => s.trim());
    includeArray = includeArray.filter((s) => s);

    value = includeArray.join(',');
    this._filter.include = value;
  }

  get limit() {
    return this._filter.limit;
  }

  set limit(value) {
    this._filter.limit = value;
  }

  get skip() {
    return this._filter.skip;
  }

  set skip(value) {
    this._filter.skip = value;
  }

  list(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize API
      this._client.request(ConversationListQuery.basePath, { method: 'get', params: this._filter }).then((res) => {
        if (!Array.isArray(res.data)) {
          return cb(null, new ConversationModel(this._client, res.data));
        }
        const conversations = [];
        res.data.forEach((conversation) => {
          conversations.push(new ConversationModel(this._client, conversation));
        });
        return cb(null, conversations);
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  count(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Check Params Validation
      const filter = this.__getCountFilter();

      // Request Channelize API
      this._client.request(`${ConversationListQuery.basePath}/count`, { method: 'get', params: filter }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }
}
