import { BaseQuery } from './base-query';
import { UserModel } from '../models/user-model';
import { errors } from '../error';
import { constants } from '../constant';
import { promiseOrCallback } from '../utils/promise';
import { Logger } from '../utils/logger';

const { INFO, WARN, ERROR } = constants.LOG_LEVELS;

export class UserListQuery extends BaseQuery {
  static basePath = constants.BASEPATH_USERS;

  constructor(client) {
    super(client);
    this._filter = {};
  }

  get online() {
    return this._filter.online;
  }

  set online(value) {
    this._filter.online = value;
  }

  get search() {
    return this._filter.search;
  }

  set search(value) {
    this._filter.search = value;
  }

  get sort() {
    return this._filter.sort;
  }

  set sort(field) {
    this._filter.sort = field;
  }

  get limit() {
    return this._filter.limit;
  }

  set limit(value) {
    this._filter.limit = value;
  }

  get skip() {
    return this._filter.skip;
  }

  set skip(value) {
    this._filter.skip = value;
  }

  get includeBlocked() {
    return this._filter.includeBlocked;
  }

  set includeBlocked(value) {
    this._filter.includeBlocked = value;
  }

  get skipUserIds() {
    return this._filter.skipUserIds;
  }

  set skipUserIds(ids) {
    this._filter.skipUserIds = ids;
  }

  get role() {
    return this._filter.role;
  }

  set role(role) {
    this._filter.role = role;
  }

  get includeDeleted() {
    return this._filter.includeDeleted;
  }

  set includeDeleted(value) {
    this._filter.includeDeleted = value;
  }

  blocksList(cb) {
    return promiseOrCallback(cb, (cb) => {
      const filter = this.__getBlockFilter();

      // Request Channelize API
      this._client.request(`${UserListQuery.basePath}/blocks`, { method: 'get', params: filter }).then((res) => {
        const blocks = [];
        res.data.forEach((block) => {
          blocks.push(new UserModel(this._client, block));
        });
        return cb(null, blocks);
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  blocksCount(cb) {
    return promiseOrCallback(cb, (cb) => {
      const filter = this.__getCountFilter(this.__getBlockFilter());

      // Request Channelize API
      this._client.request(`${UserListQuery.basePath}/blocks/count`, { method: 'get', params: filter }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  friendsList(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize API
      this._client.request(`${UserListQuery.basePath}/friends`, { method: 'get', params: this._filter }).then((res) => {
        const friends = [];
        res.data.forEach((friend) => {
          friends.push(new UserModel(this._client, friend));
        });
        return cb(null, friends);
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  friendsCount(cb) {
    return promiseOrCallback(cb, (cb) => {
      const filter = this.__getCountFilter();

      // Request Channelize API
      this._client.request(`${UserListQuery.basePath}/friends/count`, { method: 'get', params: filter }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  usersList(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize API
      this._client.request(`${UserListQuery.basePath}/`, { method: 'get', params: this._filter }).then((res) => {
        const users = [];
        res.data.forEach((user) => {
          users.push(new UserModel(this._client, user));
        });
        return cb(null, users);
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  // FILTER PARAMTETERS FOR BLOCK REQUESTS
  __getBlockFilter(filter) {
    filter = filter || this._filter;
    if (Object.prototype.hasOwnProperty.call(filter, 'includeBlocked')) {
      delete filter.includeBlocked;
    }
    if (Object.prototype.hasOwnProperty.call(filter, 'skipUserIds')) {
      delete filter.skipUserIds;
    }
    return filter;
  }
}
