import { BaseModel } from './base-model';
import { errors } from '../error';
import { constants } from '../constant';
import { promiseOrCallback } from '../utils/promise';
import { ReactionUserListQuery } from '../query/reaction-user-list-query';
import { Logger } from '../utils/logger';

const { INFO, WARN, ERROR } = constants.LOG_LEVELS;

export class MessageModel extends BaseModel {
  static __properties = {
    id: null,
    type: null,
    customType: null,
    attachments: [],
    body: null,
    conversationId: null,
    ownerId: null,
    owner: {},
    mentionedUsers: [],
    isDeleted: null,
    createdAt: null,
    updatedAt: null,
    refMsgId: null,
    metaData: null,
    parentId: null,
    parentMessage: {},
    originalMessageId: null,
    reactionsCount: {},
    reactions: {},
    replyCount: null,
    showInConversation: null,
    pinned: null,
  };

  static basePath = constants.BASEPATH_MESSAGES;

  /**
   * Delete Message
   *
   * @method deleteMessages
   * @param {Array} messageIds
   * @param {Function} cb
   * @return {Boolean}
  */
  deleteForMe(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize Api
      this._client.request(`${MessageModel.basePath}/delete`, { method: 'post', data: { messageIds: [this.id] } }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Delete Message For Everyone
   *
   * @method deleteForEveryone
   * @param {Array} messageIds
   * @param {Function} cb
   * @return {Array}
  */
  deleteForEveryone(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize Api
      this._client.request(`${MessageModel.basePath}/delete_for_everyone`, { method: 'post', data: { messageIds: [this.id] } }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Add Reaction
   *
   * @method addReaction
   * @param {object} data
   * @param {Function} cb
   * @return {Array}
  */
  addReaction(data, cb) {
    return promiseOrCallback(cb, (cb) => {
      // Check Params Validation
      if (typeof data !== 'object') {
        Logger.log(ERROR, errors.INVALID_DATA, {});
        return cb(errors.INVALID_DATA);
      }
      if (Object.prototype.hasOwnProperty.call(data, 'type') && typeof data.type !== 'string') {
        Logger.log(ERROR, errors.INVALID_REACTION_TYPE, {});
        return cb(errors.INVALID_REACTION_TYPE);
      }

      // Request Channelize Api
      this._client.request(`${MessageModel.basePath}/${this.id}/add_reaction`, { method: 'post', data }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Remove Reaction
   *
   * @method removeReaction
   * @param {object} data
   * @param {Function} cb
   * @return {Array}
  */
  removeReaction(data, cb) {
    return promiseOrCallback(cb, (cb) => {
      // Check Params Validation
      if (typeof data !== 'object') {
        Logger.log(ERROR, errors.INVALID_DATA, {});
        return cb(errors.INVALID_DATA);
      }
      if (Object.prototype.hasOwnProperty.call(data, 'type') && typeof data.type !== 'string') {
        Logger.log(ERROR, errors.INVALID_REACTION_TYPE, {});
        return cb(errors.INVALID_REACTION_TYPE);
      }

      // Request Channelize Api
      this._client.request(`${MessageModel.basePath}/${this.id}/remove_reaction`, { method: 'post', data }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Get Url Meta Data
   *
   * @method getUrlMetaData
   * @param {Function} cb
   * @return {Array}
  */
  getUrlMetaData(cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.body) {
        return cb(null, []);
      }
      const urls = this.body.match(/(?:(?:https?|ftp|file):\/\/|www\.|ftp\.)(?:\([-A-Z0-9+&@#/%=~_|$?!:,.]*\)|[-A-Z0-9+&@#/%=~_|$?!:,.])*(?:\([-A-Z0-9+&@#/%=~_|$?!:,.]*\)|[A-Z0-9])/igm) || [];

      if (!urls.length) {
        return cb(null, []);
      }

      const response = [];
      for (let index = urls.length - 1; index >= 0; index -= 1) {
        if (this._client.Cache.get(urls[index])) {
          response.push(this._client.Cache.get(urls[index]));
          urls.splice(index, 1);
        }
      }
      if (!urls.length) {
        return cb(null, response);
      }

      // Request Channelize Api
      this._client.request('/get_url_meta_data', { method: 'post', data: { urls } }).then((res) => {
        res.data.forEach((metaData) => {
          this._client.Cache.set(metaData.url, metaData);
        });

        return cb(null, res.data.concat(response));
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Pin Message
   *
   * @method pin
   * @param {Function} cb
   * @return {Array}
  */
  pin(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize Api
      this._client.request(`${MessageModel.basePath}/${this.id}/pin`, { method: 'post' }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Unpin Message
   *
   * @method unpin
   * @param {Function} cb
   * @return {Array}
  */
  unpin(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize Api
      this._client.request(`${MessageModel.basePath}/${this.id}/unpin`, { method: 'post' }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Create Reaction query builder
   *
   * @method createReactionUserListQuery
   * @return {Object} ReactionUserListQuery
  */
  createReactionUserListQuery() {
    return new ReactionUserListQuery(this._client, this.id);
  }
}
