import { BaseService } from './base-service';
import { constants } from '../constant';
import { errors } from '../error';
import { promiseOrCallback } from '../utils/promise';
import { Logger } from '../utils/logger';

const { INFO, WARN, ERROR } = constants.LOG_LEVELS;

export class PushNotificationService extends BaseService {
  static basePath = constants.BASEPATH_PUSH_NOTIFICATION_SETTINGS;

  /**
   * Get MessagingSenderId
   *
   * @method getMessagingSenderId
   * @param {Function} cb
   * @return {String} messagingSenderId
  */
  static getMessagingSenderId(cb) {
    if (!PushNotificationService.Client || !PushNotificationService.Client.getInstance()) {
      return cb('You have not initialized channelize client yet.');
    }
    const client = PushNotificationService.Client.getInstance();

    return promiseOrCallback(cb, (cb) => {
      client.request(PushNotificationService.basePath, { method: 'get', data: {} }).then((res) => {
        const messagingSenderId = res.data && res.data.firebase
        && res.data.firebase.messagingSenderId
          ? res.data.firebase.messagingSenderId
          : null;
        return cb(null, messagingSenderId);
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: client.error(err) });
        return cb(client.error(err));
      });
    });
  }

  /**
   * Create FCM Token
   *
   * @method registerFCMToken
   * @param {Function} cb
   * @return {Object}
  */
  static registerFCMToken(deviceId, token, cb) {
    if (!PushNotificationService.Client || !PushNotificationService.Client.getInstance()) {
      return cb('You have not initialized channelize client yet.');
    }
    const client = PushNotificationService.Client.getInstance();

    return promiseOrCallback(cb, (cb) => {
      if (!deviceId || typeof deviceId !== 'string') {
        Logger.log(ERROR, errors.INVALID_DEVICE_ID, {});
        return cb(errors.INVALID_DEVICE_ID);
      }
      client.request(constants.BASEPATH_PUSH_NOTIFICATION_TOKENS, {
        method: 'post',
        data: {
          deviceId,
          token,
        },
      }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: client.error(err) });
        return cb(client.error(err));
      });
    });
  }
}
