import { v4 as uuid } from 'uuid';
import { BaseModel } from './base-model';
import { MessageService } from '../services/message-service';
import { constants } from '../constant';
import { errors } from '../error';
import { Utils } from '../utils';
import { MessageListQuery } from '../query/message-list-query';
import { WatchersListQuery } from '../query/watchers-list-query';
import { ConversationBanListQuery } from '../query/conversation-ban-list-query';
import { promiseOrCallback } from '../utils/promise';
import { Logger } from '../utils/logger';

const { INFO, WARN, ERROR } = constants.LOG_LEVELS;

export class ConversationModel extends BaseModel {
  static __properties = {
    id: null,
    type: null,
    config: {},
    customType: null,
    metaData: {},
    lastMessage: {},
    lastReadAt: {},
    title: null,
    isGroup: null,
    createdAt: null,
    memberCount: 0,
    ownerId: null,
    members: [],
    profileImageUrl: null,
    isActive: null,
    isAdmin: null,
    isDeleted: null,
    mute: null,
    unreadMessageCount: null,
    updatedAt: null,
    user: {},
    watchersCount: 0,
    ban: {},
  };

  static basePath = constants.BASEPATH_CONVERSATIONS;

  __watchingEvents = [
    'watcher.conversation.start_watching',
    'watcher.conversation.stop_watching',
    'watcher.message.created',
    'watcher.message.deleted_for_everyone',
  ];

  constructor(client, data) {
    super(client, data);
    this.messageService = MessageService;
    this.__isWatching = false;
  }

  getConfig(key = null) {
    const DEFAULT_CONFIGS = {
      typing_events: true,
      read_events: true,
      push_notification: true,
      admin_messages: true,
      pin_messages: false,
      allow_watch: 'user',
      allow_chat: 'user',
      allow_delete_message_by_admin: false,
    };

    let config = this.config || {};
    config = { ...DEFAULT_CONFIGS, ...config };
    return key ? config[key] : config;
  }

  /**
   * Join a Public Conversation
   *
   * @method join
   * @param {Function} cb
   * @return {Boolean}
  */
  join(cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.isGroup) {
        Logger.log(ERROR, errors.GROUP_CONVERSATION_REQUIRED, {});
        return cb(errors.GROUP_CONVERSATION_REQUIRED);
      }

      if (this.type !== 'public') {
        Logger.log(ERROR, errors.PUBLIC_CONVERSATION_REQUIRED, {});
        return cb(errors.PUBLIC_CONVERSATION_REQUIRED);
      }

      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/join`, { method: 'post' }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Add a member as admin
   *
   * @method addAdmin
   * @param {Array} user
   * @param {Function} cb
   * @return {Boolean}
  */
  addAdmin(userId, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.isGroup) {
        Logger.log(ERROR, errors.GROUP_CONVERSATION_REQUIRED, {});
        return cb(errors.GROUP_CONVERSATION_REQUIRED);
      }
      // Check Params Validation
      if (!userId || typeof userId !== 'string') {
        Logger.log(ERROR, errors.INVALID_USER_ID, {});
        return cb(errors.INVALID_USER_ID);
      }

      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/add_admin`, { method: 'put', data: { userId } }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Remove a member as admin
   *
   * @method removeAdmin
   * @param {Array} user
   * @param {Function} cb
   * @return {Boolean}
  */
  removeAdmin(userId, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.isGroup) {
        Logger.log(ERROR, errors.GROUP_CONVERSATION_REQUIRED, {});
        return cb(errors.GROUP_CONVERSATION_REQUIRED);
      }
      // Check Params Validation
      if (!userId || typeof userId !== 'string') {
        Logger.log(ERROR, errors.INVALID_USER_ID, {});
        return cb(errors.INVALID_USER_ID);
      }

      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/remove_admin`, { method: 'put', data: { userId } }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Add Members To Conversation
   *
   * @method addMembers
   * @param {Array} memberIds
   * @param {Function} cb
   * @return {Object} ConversationModel
  */
  addMembers(memberIds, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.isGroup) {
        Logger.log(ERROR, errors.GROUP_CONVERSATION_REQUIRED, {});
        return cb(errors.GROUP_CONVERSATION_REQUIRED);
      }
      // Check Params Validation
      if (!Array.isArray(memberIds)) {
        Logger.log(ERROR, errors.INVALID_MEMBER_IDS, {});
        return cb(errors.INVALID_MEMBER_IDS);
      }

      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/add_members`, { method: 'post', data: { members: memberIds } }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Add Members To Conversation
   *
   * @method removeMembers
   * @param {Array} memberIds
   * @param {Function} cb
   * @return {Object} ConversationModel
  */
  removeMembers(memberIds, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.isGroup) {
        Logger.log(ERROR, errors.GROUP_CONVERSATION_REQUIRED, {});
        return cb(errors.GROUP_CONVERSATION_REQUIRED);
      }
      // Check Params Validation
      if (!Array.isArray(memberIds)) {
        Logger.log(ERROR, errors.INVALID_MEMBER_IDS, {});
        return cb(errors.INVALID_MEMBER_IDS);
      }

      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/remove_members`, { method: 'post', data: { members: memberIds } }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Clear Conversation
   *
   * @method clear
   * @param {Function} cb
   * @return {Boolean}
  */
  clear(cb) {
    return promiseOrCallback(cb, (cb) => {
      if (this.type !== 'private') {
        Logger.log(ERROR, errors.CAN_NOT_CLEAR_CONVERSATION, {});
        return cb(errors.CAN_NOT_CLEAR_CONVERSATION);
      }

      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/clear`, { method: 'delete' }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Delete Conversation
   *
   * @method delete
   * @param {Function} cb
   * @return {Boolean}
  */
  delete(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/delete`, { method: 'delete' }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Leave Conversation
   *
   * @method leave
   * @param {Function} cb
   * @return {Boolean}
  */
  leave(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/leave`, { method: 'post' }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Mark All Message Read
   *
   * @method markAllMessageRead
   * @param {Date} timestamp
   * @param {Function} cb
   * @return {Boolean}
  */
  markAsRead(timestamp, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.getConfig('read_events')) {
        Logger.log(ERROR, errors.CONVERSATION_NOT_CONFIGURED_READ_EVENTS, {});
        return cb(errors.CONVERSATION_NOT_CONFIGURED_READ_EVENTS);
      }

      // Request Channelize Api
      let time;
      if (timestamp) {
        time = timestamp;
      } else {
        const date = new Date();
        time = date.toISOString();
      }

      this._client.request(`${ConversationModel.basePath}/${this.id}/mark_as_read`, { method: 'put', data: { timestamp: time } }).then((res) => {
        const currentUser = this._client.getCurrentUser();
        if (currentUser && Object.prototype.hasOwnProperty.call(this.lastReadAt, currentUser.id)) {
          this.lastReadAt[currentUser.id] = time;
        }

        return cb(null, res.data);
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Update Conversation
   *
   * @method update
   * @param {Object} body
   * @param {Function} cb
   * @return {Void}
  */
  update(body, cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}`, { method: 'put', data: body }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Update Title
   *
   * @method updateTitle
   * @param {String} title
   * @param {Function} cb
   * @return {Boolean}
  */
  updateTitle(title, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.isGroup) {
        Logger.log(ERROR, errors.GROUP_CONVERSATION_REQUIRED, {});
        return cb(errors.GROUP_CONVERSATION_REQUIRED);
      }

      // Check Params Validation
      if (typeof title !== 'string') {
        Logger.log(ERROR, errors.INVALID_TITLE, {});
        return cb(errors.INVALID_TITLE);
      }

      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/update_title`, { method: 'put', data: { title } }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Mute Conversation
   *
   * @method muteConversation
   * @param {Function} cb
   * @return {Boolean}
  */
  muteConversation(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/update_settings`, { method: 'put', data: { mute: true } }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * UnMute Conversation
   *
   * @method unmute
   * @param {Function} cb
   * @return {Boolean}
  */
  unmuteConversation(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/update_settings`, { method: 'put', data: { mute: false } }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Update Profile Photo
   *
   * @method updateProfilePhoto
   * @param {FileObject} profile
   * @param {Function} cb
   * @return {Object}
  */
  updateProfilePhoto(img, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.isGroup) {
        Logger.log(ERROR, errors.GROUP_CONVERSATION_REQUIRED, {});
        return cb(errors.GROUP_CONVERSATION_REQUIRED);
      }

      // Handle if url is passed in img
      if (typeof img === 'string' && Utils.validateURL(img)) {
        const data = {
          profileImageUrl: img,
        };

        return this._client.request(`${ConversationModel.basePath}/${this.id}/update_profile`, { method: 'put', data }).then((res) => cb(null, res.data)).catch((err) => {
          Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
          return cb(err, null);
        });
      }

      // Check Params Validation
      if (typeof img !== 'object') {
        Logger.log(ERROR, errors.INVALID_PROFILE_IMG, {});
        return cb(errors.INVALID_PROFILE_IMG);
      }

      const thumbnail = false;
      this._client.File.upload(img, 'image', thumbnail, (err, res) => {
        if (err) return cb(err);

        const data = {
          profileImageUrl: res.fileUrl,
        };

        this._client.request(`${ConversationModel.basePath}/${this.id}/update_profile`, { method: 'put', data }).then((res) => cb(null, res.data)).catch((err) => {
          Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
          return cb(this._client.error(err));
        });
      });
    });
  }

  /**
   * Ban Users
   *
   * @method banUsers
   * @param {Array} userIds
   * @param {Array} expires
   * @param {Array} reason
   * @param {Function} cb
  */
  banUsers(userIds, expires = null, reason = '', cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.isGroup) {
        Logger.log(ERROR, errors.GROUP_CONVERSATION_REQUIRED, {});
        return cb(errors.GROUP_CONVERSATION_REQUIRED);
      }
      // Check Params Validation
      if (!Array.isArray(userIds)) {
        Logger.log(ERROR, errors.INVALID_USER_IDS, {});
        return cb(errors.INVALID_USER_IDS);
      }

      if (expires && typeof expires !== 'number') {
        Logger.log(ERROR, errors.INVALID_EXPIRES, {});
        return cb(errors.INVALID_EXPIRES);
      }

      if (reason && typeof reason !== 'string') {
        Logger.log(ERROR, errors.INVALID_REASON, {});
        return cb(errors.INVALID_REASON);
      }

      // Request Channelize Api
      const data = { userIds };
      if (expires) data.expires = expires;
      if (reason) data.reason = reason;
      this._client.request(`${ConversationModel.basePath}/${this.id}/ban`, { method: 'post', data }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Unban Users
   *
   * @method unbanUsers
   * @param {Array} userIds
   * @param {Function} cb
  */
  unbanUsers(userIds, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.isGroup) {
        Logger.log(ERROR, errors.GROUP_CONVERSATION_REQUIRED, {});
        return cb(errors.GROUP_CONVERSATION_REQUIRED);
      }
      // Check Params Validation
      if (!Array.isArray(userIds)) {
        Logger.log(ERROR, errors.INVALID_USER_IDS, {});
        return cb(errors.INVALID_USER_IDS);
      }

      // Request Channelize Api
      const data = { userIds };
      this._client.request(`${ConversationModel.basePath}/${this.id}/ban`, { method: 'delete', data }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Send Message
   *
   * @method sendMessage
   * @param {Object} data
   * @param {Function} cb
   * @return {Object} MessageModel
  */

  sendMessage(data = {}, cb) {
    return promiseOrCallback(cb, (cb) => {
      data.conversationId = this.id;
      data.isGroup = this.isGroup;

      this.messageService._sendMessage(data, (err, res) => {
        if (err) return cb(this._client.error(err));

        return cb(null, res);
      });
    });
  }

  /**
   * Start typing
   *
   * @method startTyping
   * @param {Function} cb
  */
  startTyping(cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.getConfig('typing_events')) {
        Logger.log(ERROR, errors.CONVERSATION_NOT_CONFIGURED_TYPING_EVENTS, {});
        return cb(errors.CONVERSATION_NOT_CONFIGURED_TYPING_EVENTS);
      }

      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/send_typing_status`, { method: 'post', data: { isTyping: true } }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Stop typing
  */
  stopTyping(cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.getConfig('typing_events')) {
        Logger.log(ERROR, errors.CONVERSATION_NOT_CONFIGURED_TYPING_EVENTS, {});
        return cb(errors.CONVERSATION_NOT_CONFIGURED_TYPING_EVENTS);
      }

      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/send_typing_status`, { method: 'post', data: { isTyping: false } }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Get Conversation Members
   *
   * @method getMembers
   * @return {Object} members
  */
  getMembers(cb) {
    if (this.members.length) {
      return cb(null, this.members);
    }

    // Get conversation members
    this._client.request(`${ConversationModel.basePath}/${this.id}/members`).then((res) => {
      this.members = res.data;
      return cb(null, res.data);
    }).catch((err) => {
      Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
      return cb(this._client.error(err));
    });
  }

  /**
   * Get Read Members
   *
   * @method getReadMembers
   * @param {Object} message
   * @return {Object} readMembers
  */
  getReadMembers(message) {
    if (typeof message !== 'object') {
      throw new Error(errors.INVALID_MESSAGE_OBJECT);
    }

    const readMembers = {};
    Object.keys(this.lastReadAt).forEach((userId) => {
      if (message.createdAt <= this.lastReadAt[userId]) {
        readMembers[userId] = this.lastReadAt[userId];
      }
    });

    return readMembers;
  }

  /**
   * Message Read By All Members
   *
   * @method readByAllMembers
   * @param {Object} message
   * @return {boolean} readByAll
  */
  readByAllMembers(message) {
    if (typeof message !== 'object') {
      throw new Error(errors.INVALID_MESSAGE_OBJECT);
    }

    let readByAll = true;
    Object.keys(this.lastReadAt).forEach((userId) => {
      if (message.createdAt >= this.lastReadAt[userId] && message.ownerId !== userId) {
        readByAll = false;
      }
    });

    return readByAll;
  }

  /**
   * Register callback to the given event
   * @param  {String}   event Event Name
   * @param  {Function} cb    Callback Function
  */
  on(event, cb) {
    if (this.__isWatchingEvent(event) && !this.__isWatching) {
      throw new Error(errors.REQUIRED_WATCHING_CONVERSATION);
    } else {
      this._client.chsocket.on(event, this.id, cb);
    }
  }

  /**
  * Check if event is a watching type or not
  * @param  {String} topic  topic name
  * @return  {Boolean}
  */
  __isWatchingEvent(event) {
    return this.__watchingEvents.includes(event);
  }

  /**
   * Unregister callback to the given event
   * @param  {String}   event Event Name
   * @param  {Function} cb    Callback Function
  */
  off(event, cb) {
    this._client.chsocket.off(event, this.id, cb);
  }

  /**
   * Start watching
   *
   * @method startWatching
   * @param {Function} cb
  */
  startWatching(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/start_watching`, { method: 'post' }).then((res) => {
        this.__isWatching = true;

        return cb(null, res.data);
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Stop watching
   *
   * @method stopWatching
   * @param {Function} cb
  */
  stopWatching(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize Api
      this._client.request(`${ConversationModel.basePath}/${this.id}/stop_watching`, { method: 'post' }).then((res) => {
        this.__isWatching = false;

        return cb(null, res.data);
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  /**
   * Check if user can watch current conversation
   *
   * @method canWatch
   */
  canWatch() {
    const currentUser = this._client.getCurrentUser();
    const allowedRole = this.getConfig('allow_watch');
    const userRole = currentUser.role;
    return Object.prototype.hasOwnProperty.call(constants.ROLE_PRECEDENCE, userRole)
      && Object.prototype.hasOwnProperty.call(constants.ROLE_PRECEDENCE, allowedRole)
      && constants.ROLE_PRECEDENCE[userRole] >= constants.ROLE_PRECEDENCE[allowedRole];
  }

  /**
   * Check if user can chat in current conversation
   *
   * @method canChat
   */
  canChat() {
    const currentUser = this._client.getCurrentUser();
    const allowedRole = this.getConfig('allow_chat');
    const userRole = currentUser.role;
    return Object.prototype.hasOwnProperty.call(constants.ROLE_PRECEDENCE, userRole)
      && Object.prototype.hasOwnProperty.call(constants.ROLE_PRECEDENCE, allowedRole)
      && constants.ROLE_PRECEDENCE[userRole] >= constants.ROLE_PRECEDENCE[allowedRole];
  }

  /**
   * Create Watchers query builder
   *
   * @method createWatchersListQuery
   * @return {Object} WatchersListQuery
  */
  createWatchersListQuery() {
    return new WatchersListQuery(this._client, this.id);
  }

  /**
   * Create Ban users query builder
   *
   * @method createBanListQuery
   * @return {Object} ConversationBanListQuery
  */
  createBanListQuery() {
    return new ConversationBanListQuery(this._client, this.id);
  }

  /**
   * Create Message query builder
   *
   * @method createMessageListQuery
   * @return {Object} MessageListQuery
  */
  createMessageListQuery() {
    return new MessageListQuery(this._client, this.id);
  }
}
