import { BaseService } from './base-service';
import { ConversationModel } from '../models/conversation-model';
import { constants } from '../constant';
import { errors } from '../error';
import { Utils } from '../utils';
import { ConversationListQuery } from '../query/conversation-list-query';
import { promiseOrCallback } from '../utils/promise';
import { Logger } from '../utils/logger';

const { INFO, WARN, ERROR } = constants.LOG_LEVELS;

export class ConversationService extends BaseService {
  static basePath = constants.BASEPATH_CONVERSATIONS;

  /**
   * Get Conversation
   *
   * @method get
   * @param {String} id
   * @param {Object} filters
   * @param {Function} cb
   * @return {Object} ConversationModel
  */
  static getConversation(id, include, cb) {
    if (!ConversationService.Client || !ConversationService.Client.getInstance()) {
      return cb('You have not initialized channelize client yet.');
    }
    const client = ConversationService.Client.getInstance();

    return promiseOrCallback(cb, (cb) => {
      // Check Params Validation
      if (typeof id !== 'string') {
        Logger.log(ERROR, errors.INVALID_ID, {});
        return cb(errors.INVALID_ID);
      }
      if (include && include !== 'members') {
        Logger.log(ERROR, errors.INVALID_CONVERSATION_INCLUDE, {});
        return cb(errors.INVALID_CONVERSATION_INCLUDE);
      }

      const params = {
        include,
      };

      // Request Channelize API
      client.request(`${ConversationService.basePath}/${id}`, { method: 'get', params }).then((res) => {
        const resModel = res.data ? new ConversationModel(client, res.data) : null;
        return cb(null, resModel);
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: client.error(err) });
        return cb(client.error(err));
      });
    });
  }

  /**
   * Create Conversation
   *
   * @method create
   * @param {Object} data
   * @param {Function} cb
   * @return {Object} ConversationModel
  */
  static createConversation(data, cb) {
    if (!ConversationService.Client || !ConversationService.Client.getInstance()) {
      return cb('You have not initialized channelize client yet.');
    }
    const client = ConversationService.Client.getInstance();

    return promiseOrCallback(cb, (cb) => {
      // Check Params Validation
      if (typeof data !== 'object') {
        Logger.log(ERROR, errors.INVALID_DATA, {});
        return cb(errors.INVALID_DATA);
      }

      data.type = data.type || 'private';
      if (['private', 'public', 'open'].indexOf(data.type) === -1) {
        Logger.log(ERROR, errors.INVALID_CONVERSATION_TYPE, {});
        return cb(errors.INVALID_CONVERSATION_TYPE);
      }

      if (Object.prototype.hasOwnProperty.call(data, 'config') && typeof data.config !== 'object') {
        Logger.log(ERROR, errors.INVALID_CONVERSATION_CONFIG, {});
        return cb(errors.INVALID_CONVERSATION_CONFIG);
      }

      if (data.type === 'private') {
        if (!Array.isArray(data.members) || data.members.length === 0) {
          Logger.log(ERROR, errors.INVALID_CONVERSATION_MEMBERS, {});
          return cb(errors.INVALID_CONVERSATION_MEMBERS);
        }
        if (!Object.prototype.hasOwnProperty.call(data, 'isGroup')) {
          data.isGroup = true;
        }
      } else {
        data.isGroup = true;
      }

      if (data.isGroup && (!data.title || typeof data.title !== 'string')) {
        Logger.log(ERROR, errors.INVALID_CONVERSATION_TITLE, {});
        return cb(errors.INVALID_CONVERSATION_TITLE);
      }

      const allowedFilters = ['id', 'title', 'members', 'isGroup', 'profileImageUrl', 'createdAt', 'type', 'config', 'customType', 'metaData'];
      const invalidFilterName = Utils.getInvalidFilter(allowedFilters, data);
      if (invalidFilterName) {
        return cb(Utils.sprintf(errors.INVALID_DATA_PARAM, invalidFilterName));
      }

      // Request Channelize API
      client.request(ConversationService.basePath, { method: 'post', data }).then((res) => cb(null, new ConversationModel(client, res.data))).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: client.error(err) });
        return cb(client.error(err));
      });
    });
  }

  /**
   * Create Conversation query builder
   *
   * @method createConversationListQuery
   * @return {Object} ConversationListQuery
  */
  static createConversationListQuery() {
    if (!ConversationService.Client || !ConversationService.Client.getInstance()) {
      throw new Error('You have not initialized channelize client yet.');
    }
    const client = ConversationService.Client.getInstance();
    return new ConversationListQuery(client);
  }
}
