import { BaseQuery } from './base-query';
import { MessageModel } from '../models/message-model';
import { errors } from '../error';
import { constants } from '../constant';
import { promiseOrCallback } from '../utils/promise';
import { Logger } from '../utils/logger';

const { INFO, WARN, ERROR } = constants.LOG_LEVELS;

export class MessageListQuery extends BaseQuery {
  static basePath = constants.BASEPATH_CONVERSATIONS;

  constructor(client, conversationId) {
    super(client);
    this._filter = {};
    this.conversationId = conversationId;
  }

  get ids() {
    return this._filter.ids;
  }

  set ids(values) {
    this._filter.ids = values;
  }

  get types() {
    return this._filter.types;
  }

  set types(type) {
    this._filter.types = type;
  }

  get customTypes() {
    return this._filter.customTypes;
  }

  set customTypes(value) {
    this._filter.customTypes = value;
  }

  get attachmentTypes() {
    return this._filter.attachmentTypes;
  }

  set attachmentTypes(type) {
    this._filter.attachmentTypes = type;
  }

  get ownerIds() {
    return this._filter.ownerIds;
  }

  set ownerIds(ids) {
    this._filter.ownerIds = ids;
  }

  get parentId() {
    return this._filter.parentId;
  }

  set parentId(id) {
    this._filter.parentId = id;
  }

  get showInConversation() {
    return this._filter.showInConversation;
  }

  set showInConversation(showInConversation) {
    this._filter.showInConversation = showInConversation;
  }

  get pinned() {
    return this._filter.pinned;
  }

  set pinned(pinned) {
    this._filter.pinned = pinned;
  }

  get sort() {
    return this._filter.sort;
  }

  set sort(sort) {
    this._filter.sort = sort;
  }

  get limit() {
    return this._filter.limit;
  }

  set limit(limit) {
    this._filter.limit = limit;
  }

  get skip() {
    return this._filter.skip;
  }

  set skip(skip) {
    this._filter.skip = skip;
  }

  list(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize Api
      this._client.request(`${MessageListQuery.basePath}/${this.conversationId}/messages`, { method: 'get', params: this._filter }).then((res) => {
        const messages = [];
        res.data.forEach((message) => {
          messages.push(new MessageModel(this._client, message));
        });
        return cb(null, messages);
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }

  count(cb) {
    return promiseOrCallback(cb, (cb) => {
      const filter = this.__getCountFilter();

      // Request Channelize Api
      this._client.request(`${MessageListQuery.basePath}/${this.conversationId}/messages/count`, { method: 'get', params: filter }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }
}
