import axios from 'axios';
import { UserService } from './services/user-service';
import { BaseService } from './services/base-service';
import { UserModel } from './models/user-model';
import { ConversationService } from './services/conversation-service';
import { MessageService } from './services/message-service';
import { PushNotificationService } from './services/push-notification-service';
import { CacheService } from './services/cache-service';
import { FileService } from './services/file-service';
import { SocketService } from './services/socket/socket-service';
import { errors } from './error';
import { constants } from './constant';
import { promiseOrCallback } from './utils/promise';
import { config } from './config';
import { Logger } from './utils/logger';

const { INFO, WARN, ERROR } = constants.LOG_LEVELS;

const { Buffer } = require('buffer');

export class Client {
  static basePath = constants.BASEPATH_USERS;

  constructor(options = {}) {
    // set the options
    options = { ...constants.DEFAULT_CLIENT_OPTIONS, ...options };

    // Make Singleton Instance
    if (Client.instance) {
      return Client.instance;
    }

    if (!options.publicKey) {
      throw new Error(errors.REQUIRED_PUBLIC_KEY);
    }

    // Check if the running environment is browser or nodejs
    this.browser = typeof options.browser !== 'undefined'
      ? options.browser
      : typeof window !== 'undefined';
    this.node = !this.browser;

    Client.instance = this;

    // Initialize All Services
    this.connected = false;
    this.publicKey = options.publicKey;
    this.options = options;

    BaseService.Client = Client;
    this.User = UserService;
    this.Message = MessageService;
    this.Conversation = ConversationService;
    this.PushNotification = PushNotificationService;
    this.Cache = CacheService;
    this.File = FileService;
    this.chsocket = new SocketService(options);
  }

  /**
   * Provide the instance of Client
   *
   * @method getInstance
   * @return {Object} Channelize
  */
  static getInstance() {
    return Client.instance;
  }

  /**
   * Create a real time connection with Channelize server
   *
   * @method connect
   * @param {String} userId
   * @param {String} accessToken
   * @param {Function} cb
   * @return {Boolean} true
  */
  connect(userId, accessToken, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (this.connected) {
        Logger.log(ERROR, errors.ALREADY_CONNECTED, {});
        return cb(errors.ALREADY_CONNECTED);
      }

      if (!userId || typeof userId !== 'string') {
        Logger.log(ERROR, errors.INVALID_USER_ID, {});
        return cb(errors.INVALID_USER_ID);
      }

      if (!accessToken || typeof accessToken !== 'string') {
        Logger.log(ERROR, errors.INVALID_ACCESS_TOKEN, {});
        return cb(errors.INVALID_ACCESS_TOKEN);
      }

      if (this.browser && 'localStorage' in window) {
        const localUser = this.__getLocalUser(userId, accessToken);
        if (localUser) {
          return this.__connect(localUser, accessToken, cb);
        }
      }

      this.__verifyAccessToken(userId, accessToken, (err, res) => {
        if (err) return cb(err);

        res.user.accessToken = accessToken; // support guest user login via connect

        if (this.browser && 'localStorage' in window) {
          this.__setLocalUser(res.user, accessToken);
        }

        this.__connect(res.user, accessToken, cb);
      });
    });
  }

  /**
   * Create a real time connection with Channelize server for anonymous users
   *
   * @method connectAsAnonymous
   * @param {Function} cb
   * @return {Boolean} true
  */
  connectAsAnonymous(cb) {
    return promiseOrCallback(cb, (cb) => {
      if (this.connected) {
        Logger.log(ERROR, errors.ALREADY_CONNECTED, {});
        return cb(errors.ALREADY_CONNECTED);
      }

      if (this.browser && 'localStorage' in window) {
        const user = this.__getLocalAnonymousUser();
        if (user) {
          return this.__connectAsAnonymous(user, cb);
        }
      }

      const randomNumber = Math.random().toString(10).substr(2, 10);
      const user = {
        id: `${constants.ANONYMOUS_ID_PREFIX}-${randomNumber}`,
        displayName: constants.ANONYMOUS_USER_DISPLAY_NAME,
        role: constants.ROLES.ANONYMOUS,
      };

      if (this.browser && 'localStorage' in window) {
        this.__setLocalAnonymousUser(user);
      }

      this.__connectAsAnonymous(user, cb);
    });
  }

  /**
   * Create a real time connection with Channelize server using guest user
   *
   * @method connect
   * @param {String} userId
   * @param {String} accessToken
   * @param {Function} cb
   * @return {Boolean} true
  */
  connectAsGuest(user, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (this.connected) {
        return cb(errors.ALREADY_CONNECTED);
      }

      const guest = {};
      if (typeof user === 'string') {
        guest.displayName = user;
      } else if (typeof user === 'object') {
        if (user.displayName) guest.displayName = user.displayName;
        if (user.profileImageUrl) {
          guest.profileImageUrl = user.profileImageUrl;
        }
      } else {
        Logger.log(ERROR, errors.INVALID_GUEST, {});
        return cb(errors.INVALID_GUEST);
      }

      if (this.browser && 'localStorage' in window) {
        const guestUser = this.__getLocalGuestUser(guest.displayName);
        if (guestUser) {
          return this.__connect(guestUser, guestUser.accessToken, cb);
        }
      }

      this.User.createGuest(guest, (err, res) => {
        if (err) return cb(err);

        if (this.browser && 'localStorage' in window) {
          this.__setLocalGuestUser(res);
        }

        this.__connect(res, res.accessToken, cb);
      });
    });
  }

  /**
   * Switch to guest user from anonymous user
   *
   * @method connect
   * @param {String} userId
   * @param {String} accessToken
   * @param {Function} cb
   * @return {Boolean} true
  */
  switchToGuest(user, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.connected) {
        Logger.log(ERROR, errors.NOT_CONNECTED, {});
        return cb(errors.NOT_CONNECTED);
      }

      if (!this.isAnonymousUser()) {
        Logger.log(ERROR, errors.NOT_ANONYMOUS_USER, {});
        return cb(errors.NOT_ANONYMOUS_USER);
      }

      const guest = {
        anonymousUserId: this.getCurrentUser().id,
      };
      if (typeof user === 'string') {
        guest.displayName = user;
      } else if (typeof user === 'object') {
        if (user.displayName) guest.displayName = user.displayName;
        if (user.profileImageUrl) {
          guest.profileImageUrl = user.profileImageUrl;
        }
      } else {
        Logger.log(ERROR, errors.INVALID_GUEST, {});
        return cb(errors.INVALID_GUEST);
      }

      if (this.browser && 'localStorage' in window) {
        const guestUser = this.__getLocalGuestUser(guest.displayName);
        if (guestUser) {
          return this.__switchToGuest(guestUser, cb);
        }
      }

      this.User.createGuest(guest, (err, res) => {
        if (err) return cb(err);

        if (this.browser && 'localStorage' in window) {
          this.__setLocalGuestUser(res);
        }

        this.__switchToGuest(res, cb);
      });
    });
  }

  /**
   * Validate user and access token and return locally stored user
   *
   * @method __getLocalUser
   * @param {String} userId
   * @param {String} accessToken
   * @return {Object} Local User Object
  */
  __getLocalUser(userId, accessToken) {
    const key = constants.LOCALSTORAGE_PREFIX + this.publicKey;
    let localData = window.localStorage.getItem(key);

    if (!localData) {
      return false;
    }

    try {
      localData = JSON.parse(localData);
    } catch (e) {
      return false;
    }

    if (!localData.user || !localData.accessToken) {
      return false;
    }

    if (this.__btoa(accessToken) === localData.accessToken && userId === localData.user.id) {
      return new UserModel(this, localData.user);
    }
    return false;
  }

  getCurrentUser() {
    return this.currentUser;
  }

  __btoa = (string) => Buffer.from(string).toString('base64')

  /**
   * Store user and access token locally
   *
   * @method __setLocalUser
   * @param {String} userId
   * @param {String} accessToken
  */
  __setLocalUser(user, accessToken) {
    const key = constants.LOCALSTORAGE_PREFIX + this.publicKey;
    const localData = {
      user: user.toJSON(),
      accessToken: this.__btoa(accessToken),
      expires: Date.now() + constants.LOCALSTORAGE_EXPIRY_TIME,
    };
    window.localStorage.setItem(key, JSON.stringify(localData));
  }

  /**
   * Remove locally stored user object and access token
  */
  __removeLocalUser() {
    const key = constants.LOCALSTORAGE_PREFIX + this.publicKey;
    window.localStorage.removeItem(key);
  }

  /**
   * Create a real time connection with Channelize server
   *
   * @method __connect
   * @param {Object} user
   * @param {String} accessToken
   * @param {Function} cb
   * @return {Boolean} true
  */
  __connect(user, accessToken, cb) {
    return promiseOrCallback(cb, (cb) => {
      const credentials = { userName: user.id, password: '' };
      // connect to mqtt server
      this.chsocket.connect(user, credentials, (err, res) => {
        if (err) {
          Logger.log(ERROR, errors.WEBSOCKET_CONNECTION_ERROR, { err });
          return cb(err);
        }

        this.accessToken = this.__btoa(accessToken);
        this.currentUser = user;
        this.connected = true;

        Logger.log(INFO, 'Connected to SDK as a user', {
          user: this.currentUser,
        });

        return cb(null, this.currentUser);
      });
    });
  }

  /**
   * Create a real time connection connection with Channelize server for anonymous user
   *
   * @method __connectAsAnonymous
   * @param {Object} user
   * @param {Function} cb
   * @return {Boolean} true
  */
  __connectAsAnonymous(user, cb) {
    return promiseOrCallback(cb, (cb) => {
      // connect to mqtt server
      this.chsocket.connect(user, null, (err, res) => {
        if (err) {
          Logger.log(ERROR, errors.WEBSOCKET_CONNECTION_ERROR, { err });
          return cb(err);
        }

        this.currentUser = user;
        this.connected = true;

        Logger.log(INFO, 'Connected to SDK as an anonymous user', {
          user: this.currentUser,
        });

        return cb(null, this.currentUser);
      });
    });
  }

  // Swith to guest from anonymous
  __switchToGuest(res, cb) {
    this.accessToken = this.__btoa(res.accessToken);
    this.currentUser = res;

    this.chsocket.updateCurrentUser(this.currentUser);

    Logger.log(INFO, 'Switched from anonymous user to guest user', {
      user: this.currentUser,
    });

    return cb(null, this.currentUser);
  }

  /**
   * Disconnect connection with Channelize server
   *
   * @method disconnect
   * @param {String} deviceId
   * @param {Function} cb
   * @return {Boolean} true
  */
  disconnect(deviceId, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.connected) {
        Logger.log(ERROR, errors.ALREADY_DISCONNECTED, {});
        return cb(errors.ALREADY_DISCONNECTED);
      }

      if (deviceId && typeof deviceId !== 'string') {
        Logger.log(ERROR, errors.INVALID_DEVICE_ID, {});
        return cb(errors.INVALID_DEVICE_ID);
      }

      if (this.isAnonymousUser() || this.isGuestUser()) {
        return this.__disconnect(cb);
      }

      this.__logout(deviceId, (err, res) => {
        if (err) return cb(err);
        this.__disconnect(cb);
      });
    });
  }

  __disconnect(cb) {
    return promiseOrCallback(cb, (cb) => {
      // disconnect mqtt connection
      this.chsocket.__disconnect();

      this.currentUser = null;
      this.accessToken = null;
      this.connected = false;

      // Remove local storage data of user
      if (this.browser && 'localStorage' in window) {
        this.__removeLocalUser();
      }

      Logger.log(INFO, 'Disconnected with SDK', {});

      cb();
    });
  }

  /**
   * Login into channelize with Email and Password
   *
   * @method loginWithEmailPassword
   * @param {String} email
   * @param {String} password
   * @param {Function} cb
   * @return {Object}
  */
  loginWithEmailPassword(email, password, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!email || !this.__isValidEmail(email)) {
        Logger.log(ERROR, errors.INVALID_EMAIL, {});
        return cb(errors.INVALID_EMAIL);
      }

      if (!password || typeof password !== 'string') {
        Logger.log(ERROR, errors.INVALID_PASSWORD, {});
        return cb(errors.INVALID_PASSWORD);
      }

      // Request Channelize API
      this.request(`${Client.basePath}/login`, {
        method: 'post',
        data: {
          authenticationType: 1,
          email,
          password,
        },
      }).then((res) => {
        res.data.user = new UserModel(this, res.data.user);
        return cb(null, res.data);
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this.error(err) });
        return cb(this.error(err));
      });
    });
  }

  /**
   * Login into channelize with user id and clientServerToken
   *
   * @method loginWithUserId
   * @param {String} userId
   * @param {String} clientServerToken
   * @param {Function} cb
   * @return {Object}
  */
  loginWithUserId(userId, clientServerToken, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!userId || typeof userId !== 'string') {
        Logger.log(ERROR, errors.INVALID_USER_ID, {});
        return cb(errors.INVALID_USER_ID);
      }

      if (!clientServerToken || typeof clientServerToken !== 'string') {
        Logger.log(ERROR, errors.INVALID_CLIENT_SERVER_TOKEN, {});
        return cb(errors.INVALID_CLIENT_SERVER_TOKEN);
      }

      // Request Channelize API
      this.request(`${Client.basePath}/login`, {
        method: 'post',
        data: {
          authenticationType: 0,
          userId,
          pmClientServerToken: clientServerToken,
        },
      }).then((res) => {
        res.data.user = new UserModel(this, res.data.user);
        return cb(null, res.data);
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this.error(err) });
        return cb(this.error(err));
      });
    });
  }

  // Check if current user role is anonymous.
  isAnonymousUser() {
    return this.currentUser && this.currentUser.role === constants.ROLES.ANONYMOUS;
  }

  // Check if current user role is guest.
  isGuestUser() {
    return this.currentUser && this.currentUser.role === constants.ROLES.GUEST;
  }

  // Return if connected via private key/server side
  isServerConnection = () => false

  /**
   * Set Log Level to debug
   *
   * @method setLogLevel
   * @param {String} level
  */
  setLogLevel = (level) => {
    const levels = Object.values(constants.LOG_LEVELS);
    if (levels.includes(level)) {
      Logger.setLogLevel(level);
    } else {
      throw new Error(errors.INVALID_LOG_LEVEL);
    }
  }

  /**
   * Set custom logger
   *
   * @method setLogger
   * @param {Function} logger
  */
  setLogger = (logger) => {
    // Logger should be a function accepting three arguments
    if (logger && typeof logger === 'function' && logger.length === 3) {
      Logger.setLogger(logger);
    } else {
      throw new Error(errors.INVALID_LOGGER);
    }
  }

  // Get anonymous user saved in local storage
  __getLocalAnonymousUser() {
    const key = constants.LOCALSTORAGE_PREFIX + this.publicKey;
    let localData = window.localStorage.getItem(key);

    if (!localData) {
      return false;
    }

    try {
      localData = JSON.parse(localData);
    } catch (e) {
      return false;
    }

    if (!localData.user || localData.user.role !== constants.ROLES.ANONYMOUS) {
      return false;
    }

    return localData.user;
  }

  // Set anonymous user local storage
  __setLocalAnonymousUser(user) {
    const key = constants.LOCALSTORAGE_PREFIX + this.publicKey;
    const localData = {
      user,
      expires: Date.now() + constants.LOCALSTORAGE_EXPIRY_TIME,
    };
    window.localStorage.setItem(key, JSON.stringify(localData));
  }

  // Get guest user saved in local storage
  __getLocalGuestUser(displayName) {
    const key = constants.LOCALSTORAGE_PREFIX + this.publicKey;
    let localData = window.localStorage.getItem(key);

    if (!localData) {
      return false;
    }

    try {
      localData = JSON.parse(localData);
    } catch (e) {
      return false;
    }

    if (!localData.user || localData.user.role !== constants.ROLES.GUEST) {
      return false;
    }

    if (localData.user.accessToken && displayName === localData.user.displayName) {
      return new UserModel(this, localData.user);
    }
    return false;
  }

  // Set guest user local storage
  __setLocalGuestUser(user) {
    const key = constants.LOCALSTORAGE_PREFIX + this.publicKey;
    const localData = {
      user,
      accessToken: this.__btoa(user.accessToken),
      expires: Date.now() + constants.LOCALSTORAGE_EXPIRY_TIME,
    };
    window.localStorage.setItem(key, JSON.stringify(localData));
  }

  /**
   * Verify the given access token
   *
   * @method __verifyAccessToken
   * @param {String} accessToken
   * @param {Function} cb
   * @return {Object} UserModel
  */
  __verifyAccessToken(userId, accessToken, cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize API
      this.request(`${Client.basePath}/verify_access_token`, {
        method: 'post',
        data: {
          accessToken,
        },
      }).then((res) => {
        res.data.user = new UserModel(this, res.data.user);
        return cb(null, res.data);
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this.error(err) });
        return cb(this.error(err));
      });
    });
  }

  /**
   * Logout
   *
   * @method __logout
   * @param {String} accessToken
   * @param {Function} cb
   * @return {Object} UserModel
  */
  __logout(deviceId, cb) {
    return promiseOrCallback(cb, (cb) => {
      this.request(`${Client.basePath}/logout`, {
        method: 'post',
        data: {
          deviceId,
        },
      }).then((res) => cb(null, res)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this.error(err) });
        return cb(this.error(err));
      });
    });
  }

  __isValidEmail = (email) => /\S+@\S+\.\S+/.test(email)

  /**
   * Request API
   *
   * @method request
   * @param {path} Request Path
   * @param {Object} Request Params
   * @return {Promise}
  */
  request(path, params) {
    if (!params) params = {};

    let url = path;
    if (!params.absolute) {
      url = config.BASE_URL + path;
    }

    params.headers = params.headers || {};
    params.headers['Public-Key'] = this.publicKey;

    if (!params.noAuth && this.accessToken) {
      params.headers.Authorization = `Bearer ${this.accessToken}`;
    }

    if (this.isAnonymousUser()) {
      params.headers['Anonymous-User-Id'] = this.currentUser.id;
    }

    return axios(url, params);
  }

  /**
   * Return error message from axios error object
   *
   * @method error
   * @param {Object} err
   * @return {Object}
  */
  error = (error) => {
    if (error.response) {
      // The request was made and the server responded with a status code
      // that falls out of the range of 2xx
      return error.response.data.error || error.response.data;
    }
    return error;
  }
}
