import axios from 'axios';
import { UserModel } from './models/user-model';
import { UserService } from './services/user-service';
import { BaseService } from './services/base-service';
import { ConversationService } from './services/conversation-service';
import { MessageService } from './services/message-service';
import { PushNotificationService } from './services/push-notification-service';
import { CacheService } from './services/cache-service';
import { FileService } from './services/file-service';
import { SocketService } from './services/socket/socket-service';
import { errors } from './error';
import { constants } from './constant';
import { promiseOrCallback } from './utils/promise';
import { config } from './config';
import { Logger } from './utils/logger';

const { INFO, WARN, ERROR } = constants.LOG_LEVELS;

const { Buffer } = require('buffer');

export class Server {
  static basePath = constants.BASEPATH_USERS;

  constructor(options = {}) {
    // set the options
    options = { ...constants.DEFAULT_SERVER_OPTIONS, ...options };

    // Make Singleton Instance
    if (Server.instance) {
      return Server.instance;
    }

    if (!options.publicKey) {
      throw new Error(errors.REQUIRED_PUBLIC_KEY);
    }

    if (!options.privateKey) {
      throw new Error(errors.REQUIRED_PRIVATE_KEY);
    }

    // Check if the running environment is browser or nodejs
    this.browser = typeof options.browser !== 'undefined'
      ? options.browser
      : typeof window !== 'undefined';
    this.node = !this.browser;

    Server.instance = this;

    // Initialize All Services
    this.connected = false;
    this.publicKey = options.publicKey;
    this.privateKey = options.privateKey;
    this.options = options;

    BaseService.Client = Server;
    this.User = UserService;
    this.Message = MessageService;
    this.Conversation = ConversationService;
    this.PushNotification = PushNotificationService;
    this.Cache = CacheService;
    this.File = FileService;
    this.chsocket = new SocketService(options);
  }

  /**
   * Provide the instance of Client
   *
   * @method getInstance
   * @return {Object} Channelize
  */
  static getInstance() {
    return Server.instance;
  }

  /**
   * Create a real time connection with Channelize server
   *
   * @method connect
   * @param {String} userId
   * @param {Function} cb
   * @return {Boolean} true
  */
  connect(userId, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (this.connected) {
        Logger.log(ERROR, errors.ALREADY_CONNECTED, {});
        return cb(errors.ALREADY_CONNECTED);
      }

      if (!userId) {
        return this.__connect({}, cb);
      }

      this.User.get(userId, (err, user) => {
        if (err) return cb(err);

        return this.__connect(user, cb);
      });
    });
  }

  setCurrentUser(userId, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!userId) {
        return this.__setCurrentUser({}, cb);
      }

      this.User.get(userId, (err, user) => {
        if (err) return cb(err);

        return this.__setCurrentUser(user, cb);
      });
    });
  }

  /**
   * Disconnect connection with Channelize server
   *
   * @method disconnect
   * @param {Function} cb
   * @return {Boolean} true
  */
  disconnect(cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!this.connected) {
        Logger.log(ERROR, errors.ALREADY_DISCONNECTED, {});
        return cb(errors.ALREADY_DISCONNECTED);
      }

      this.__disconnect(cb);
    });
  }

  /**
   * Create Access tokens for a user
   *
   * @method createAccessToken
   * @param {Function} cb
   * @return {Boolean} true
  */
  createAccessToken(userId, cb) {
    return promiseOrCallback(cb, (cb) => {
      if (!userId || typeof userId !== 'string') {
        Logger.log(ERROR, errors.INVALID_USER_ID, {});
        return cb(errors.INVALID_USER_ID);
      }

      // Request Channelize API
      this.request(`${Server.basePath}/create_access_token`, { method: 'post', data: { userId } }).then((res) => {
        res.data.user = new UserModel(this, res.data.user);
        return cb(null, res.data);
      }).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this.error(err) });
        return cb(this.error(err));
      });
    });
  }

  getCurrentUser() {
    return this.currentUser;
  }

  // Check if current user role is guest.
  isGuestUser() {
    return this.currentUser && this.currentUser.role === constants.ROLES.GUEST;
  }

  // Return if connected via private key/server side
  isServerConnection = () => true

  /**
   * Set Log Level to debug
   *
   * @method setLogLevel
   * @param {String} level
  */
  setLogLevel = (level) => {
    const levels = Object.values(constants.LOG_LEVELS);
    if (levels.includes(level)) {
      Logger.setLogLevel(level);
    } else {
      throw new Error(errors.INVALID_LOG_LEVEL);
    }
  }

  /**
   * Set custom logger
   *
   * @method setLogger
   * @param {Function} logger
  */
  setLogger = (logger) => {
    // Logger should be a function accepting three arguments
    if (logger && typeof logger === 'function' && logger.length === 3) {
      Logger.setLogger(logger);
    } else {
      throw new Error(errors.INVALID_LOGGER);
    }
  }

  /**
   * Create a real time connection with Channelize server
   *
   * @method __connect
   * @param {Object} user
   * @param {Function} cb
   * @return {Boolean} true
  */
  __connect(user, cb) {
    return promiseOrCallback(cb, (cb) => {
      const credentials = { userName: user.id, password: '' };
      // connect to mqtt server
      this.chsocket.connect(user, credentials, (err, res) => {
        if (err) {
          Logger.log(ERROR, errors.WEBSOCKET_CONNECTION_ERROR, { err });
          return cb(err);
        }

        // this.accessToken = this.__btoa(accessToken);
        this.currentUser = user;
        this.connected = true;

        Logger.log(INFO, 'Connected to SDK as a user', {
          user: this.currentUser,
        });

        return cb(null, this.currentUser);
      });
    });
  }

  // Set current user in socket
  __setCurrentUser(user, cb) {
    this.currentUser = user;
    this.chsocket.updateCurrentUser(this.currentUser);

    Logger.log(INFO, 'Current User changed in SDK', {
      user: this.currentUser,
    });

    cb(null, this.currentUser);
  }

  // Disconnect the channelize client
  __disconnect(cb) {
    return promiseOrCallback(cb, (cb) => {
      // disconnect mqtt connection
      this.chsocket.__disconnect();

      this.currentUser = null;
      // this.accessToken = null;
      this.connected = false;

      Logger.log(INFO, 'Disconnected with SDK', {});

      cb();
    });
  }

  // convert string to base64
  __btoa = (string) => Buffer.from(string).toString('base64')

  /**
   * Request API
   *
   * @method request
   * @param {path} Request Path
   * @param {Object} Request Params
   * @return {Promise}
  */
  request(path, params) {
    if (!params) params = {};

    let url = path;
    if (!params.absolute) {
      url = config.BASE_URL + path;
    }

    params.headers = params.headers || {};

    params.headers['Public-Key'] = this.publicKey;
    if (!params.noAuth && this.privateKey) {
      params.headers.Authorization = `Basic ${this.__btoa(this.privateKey)}`;
    }

    const currentUser = this.getCurrentUser();
    if (currentUser && currentUser.id) {
      params.headers['User-Id'] = currentUser.id;
    }

    return axios(url, params);
  }

  /**
   * Return error message from axios error object
   *
   * @method error
   * @param {Object} err
   * @return {Object}
  */
  error = (error) => {
    if (error.response) {
      // The request was made and the server responded with a status code
      // that falls out of the range of 2xx
      return error.response.data.error || error.response.data;
    }
    return error;
  }
}
