import { BaseQuery } from './base-query';
import { UserModel } from '../models/user-model';
import { constants } from '../constant';
import { promiseOrCallback } from '../utils/promise';
import { Logger } from '../utils/logger';
import { errors } from '../error';

const { INFO, WARN, ERROR } = constants.LOG_LEVELS;
export class ConversationBanListQuery extends BaseQuery {
  static basePath = constants.BASEPATH_CONVERSATIONS;

  constructor(client, conversationId) {
    super(client);
    this._filter = {};
    this.conversationId = conversationId;
  }

  get limit() {
    return this._filter.limit;
  }

  set limit(limit) {
    this._filter.limit = limit;
  }

  get skip() {
    return this._filter.skip;
  }

  set skip(skip) {
    this._filter.skip = skip;
  }

  list(cb) {
    return promiseOrCallback(cb, (cb) => {
      // Request Channelize Api
      this._client.request(`${ConversationBanListQuery.basePath}/${this.conversationId}/ban`, { method: 'get', params: this._filter }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: this._client.error(err) });
        return cb(this._client.error(err));
      });
    });
  }
}
