import * as AWS from 'aws-sdk/global';
import { promiseOrCallback } from '../utils/promise';
import { config } from '../config';
import { Logger } from '../utils/logger';
import { constants } from '../constant';

const awsIot = require('aws-iot-device-sdk');

const { INFO, WARN, ERROR } = constants.LOG_LEVELS;

export class Mqtt {
  constructor(chsocket, options) {
    this.chsocket = chsocket;
    this.options = options;
    // Identify if reconnect event has been called
    this.__reconnected = false;

    // This is the time in min after that new credentials will be generated
    this.refreshTokenTime = 55;
    this.refreshTimer = null;
  }

  /**
   * Create a connection with MQTT server
   * @param  {String}   userId      User Id
   * @param  {Object}   credentials userName and password
   * @param  {Function} cb          Callback
  */
  connect(userId, credentials, cb) {
    return promiseOrCallback(cb, (cb) => {
      AWS.config.region = config.AWS_REGION; // Region
      AWS.config.credentials = new AWS.CognitoIdentityCredentials({
        IdentityPoolId: config.AWS_COGNITO_IDENTITY_POOL_ID,
      });

      return AWS.config.credentials.get((err, data) => {
        if (!err) {
          // console.log('retrieved identity: ' + AWS.config.credentials.identityId);

          // Refresh the credentials after every 55 min
          this.refresh(userId, cb);

          return this._connectToAWSIOT(userId, AWS.config.credentials, credentials, cb);
        }

        Logger.log(ERROR, 'error retrieving identity', { err });
        cb(err);
      });
    });
  }

  refresh(userId, cb) {
    this.refreshTimer = setInterval(() => {
      AWS.config.region = config.AWS_REGION; // Region
      AWS.config.credentials = new AWS.CognitoIdentityCredentials({
        IdentityPoolId: config.AWS_COGNITO_IDENTITY_POOL_ID,
      });

      return AWS.config.credentials.get((err, data) => {
        if (!err) {
          // console.log('retrieved identity: ' + AWS.config.credentials.identityId);

          if (this.__client) {
            this.__client.updateWebSocketCredentials(
              AWS.config.credentials.accessKeyId,
              AWS.config.credentials.secretAccessKey,
              AWS.config.credentials.sessionToken,
            );
          }
        } else {
          Logger.log(ERROR, 'error retrieving identity', { err });
          cb(err);
        }
      });
    }, this.refreshTokenTime * 60 * 1000);
  }

  /**
   * Connect to aws IOT
   * @param  {String}   userId      User Id
   * @param  {Object}   awsCredentials AccessKeyId, SecretKey & SessionToken
   * @param  {Object}   credentials userName and password
   * @param  {Function} cb          Callback
  */
  _connectToAWSIOT(userId, awsCredentials, credentials, cb) {
    return promiseOrCallback(cb, (cb) => {
      const clientId = this.__generateClientId(userId);

      let will = null;
      if (this.chsocket.__hasUserRole() && this.options.allowUserPresenceEvents) {
        // will message - the last message that will be automatically sent on disconnecting
        will = {
          topic: `${this.chsocket.__publicKey}/users/server/offline`,
          payload: JSON.stringify({ userId, version: 'v2' }),
          qos: 0,
        };
      }

      let options = {
        clientId,
        protocol: 'wss',
        host: config.AWS_IOT_HOST,
        accessKeyId: awsCredentials.accessKeyId,
        secretKey: awsCredentials.secretAccessKey,
        sessionToken: awsCredentials.sessionToken,
        region: config.AWS_REGION,
        will,
        keepalive: 300,
      };

      options = Object.assign(options, credentials);
      this.__client = awsIot.device(options);

      // Event will be called when successful connection / reconnection is made.
      this.__client.on('connect', (connack) => {
        // console.log(`${new Date()} channelize :: websockets :: mqtt :: connect`, connack);
        this.chsocket.__onConnected(this.__reconnected);
        if (!this.__reconnected) {
          cb(null, true);
        }
      });

      // Event will be called when connection lost
      this.__client.on('offline', (err) => {
        // console.log(`${new Date()} channelize :: websockets :: mqtt :: offline`, err);
      });

      // Event will be called when connection lost
      this.__client.on('close', (err) => {
        // console.log(`${new Date()} channelize :: websockets :: mqtt :: close`, err);
        if (this.chsocket.__connected) {
          this.chsocket.__onConnectionLost(err);
        }
      });

      // Event will be called when reconnect process starts
      this.__client.on('reconnect', () => {
        // console.log(`${new Date()} channelize :: websockets :: mqtt :: reconnect`);
        this.__reconnected = true;
      });

      // Event will be called when there is any kind of error
      this.__client.on('error', (err) => {
        // console.log(`${new Date()} channelize :: websockets :: mqtt :: err`, err);
        if (!this.__reconnected) {
          cb(err);
        }
      });

      // Event will be called when any message is received.
      this.__client.on('message', (topic, message, packet) => {
        // console.log(`${new Date()} channelize :: websockets :: mqtt :: message`, topic);
        const payload = JSON.parse(message);
        this.chsocket.__trigger(topic, payload);
      });
    });
  }

  /**
  * Disconnect with mqtt server
  */
  __disconnect() {
    this.__client.end();
    clearInterval(this.refreshTimer);
  }

  /**
  * Subscribe a topic to mqtt server
  * @param  {String} topic  Topic
  */
  __subscribe(topic) {
    this.__client.subscribe(topic);
  }

  /**
  * Unsubscribe a topic to mqtt server
  * @param  {String} topic  Topic
  */
  __unsubscribe(topic) {
    this.__client.unsubscribe(topic);
  }

  /**
  * Publish a topic to mqtt server
  * @param  {String} topic  Topic
  */
  __publish(topic, payload) {
    this.__client.publish(topic, payload);
  }

  /**
   * Generate random client Id for mqtt connection
   * @return  {String}   Random client Id
  */
  __generateClientId = (userId) => `client-${userId}-${Math.random().toString(36).substr(2, 19)}`
}
