import { Client } from './core/client';
import { Server } from './core/server';
import { Core } from './core/index';

if (global.Channelize) {
  console.error('ERROR: It appears that you have multiple copies of the Channelize WebSDK in your build!'); // eslint-disable-line no-console
} else {
  global.Channelize = {
    core: Core,
    client: Client,
    server: Server,
  };
}

export const { Channelize } = global;
export const { core, client, server } = global.Channelize;
