export class BaseQuery {
  constructor(client) {
    this._client = client;
  }

  // Remove filters not needed in count requests
  __getCountFilter(filter) {
    filter = filter || this._filter;
    filter = { ...filter };
    if (Object.prototype.hasOwnProperty.call(filter, 'include')) {
      delete filter.include;
    }
    if (Object.prototype.hasOwnProperty.call(filter, 'sort')) {
      delete filter.sort;
    }
    if (Object.prototype.hasOwnProperty.call(filter, 'skip')) {
      delete filter.skip;
    }
    if (Object.prototype.hasOwnProperty.call(filter, 'limit')) {
      delete filter.limit;
    }
    return filter;
  }
}
