import { BaseService } from './base-service';
import { MessageModel } from '../models/message-model';
import { constants } from '../constant';
import { errors } from '../error';
import { Utils } from '../utils';
import { promiseOrCallback } from '../utils/promise';
import { Logger } from '../utils/logger';

const { INFO, WARN, ERROR } = constants.LOG_LEVELS;

export class MessageService extends BaseService {
  static basePath = constants.BASEPATH_MESSAGES;

  /**
   * Get Unread Messages Count
   *
   * @method getTotalUnreadMessageCount
   * @param {Object} filters
   * @param {Function} cb
   * @return {Object}
  */
  static getTotalUnreadMessageCount(cb) {
    if (!MessageService.Client || !MessageService.Client.getInstance()) {
      return cb('You have not initialized channelize client yet.');
    }
    const client = MessageService.Client.getInstance();

    return promiseOrCallback(cb, (cb) => {
      // Request Channelize API
      client.request(`${MessageService.basePath}/unread/count`, { method: 'get' }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: client.error(err) });
        return cb(client.error(err));
      });
    });
  }

  /**
   * Forward Messages
   *
   * @method forward
   * @param {Array} messageIds
   * @param {Array} userIds
   * @param {Function} cb
   * @return {Object} MessageModel
  */
  static forwardMessages(messageIds, userIds, conversationIds, cb) {
    if (!MessageService.Client || !MessageService.Client.getInstance()) {
      return cb('You have not initialized channelize client yet.');
    }
    const client = MessageService.Client.getInstance();

    return promiseOrCallback(cb, (cb) => {
      // Check Params Validation
      if (!Array.isArray(messageIds) && messageIds.length) {
        Logger.log(ERROR, errors.INVALID_MESSAGE_IDS, {});
        return cb(errors.INVALID_MESSAGE_IDS);
      }

      if (!Array.isArray(userIds)) {
        Logger.log(ERROR, errors.INVALID_USER_IDS, {});
        return cb(errors.INVALID_USER_IDS);
      }

      if (!Array.isArray(conversationIds)) {
        Logger.log(ERROR, errors.INVALID_CONVERSATION_IDS, {});
        return cb(errors.INVALID_CONVERSATION_IDS);
      }

      if (!userIds.length && !conversationIds.length) {
        Logger.log(ERROR, errors.REQUIRED_USER_OR_CONVERSATION, {});
        return cb(errors.REQUIRED_USER_OR_CONVERSATION);
      }

      const data = {
        messageIds,
      };

      if (userIds.length) {
        data.userIds = userIds;
      } else if (conversationIds.length) {
        data.conversationIds = conversationIds;
      }

      // Request Channelize Api
      client.request(`${MessageService.basePath}/forward`, { method: 'post', data }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: client.error(err) });
        return cb(client.error(err));
      });
    });
  }

  /**
   * Send Message
   *
   * @method send
   * @param {Object} data
   * @param {Function} cb
   * @return {Object} MessageModel
  */
  static sendMessage(data = {}, cb) {
    return promiseOrCallback(cb, (cb) => {
      // Check Params Validation
      if (!data.userId || typeof data.userId !== 'string') {
        Logger.log(ERROR, errors.INVALID_USER_ID, {});
        return cb(errors.INVALID_USER_ID);
      }

      this._sendMessage(data, (err, res) => {
        if (err) return cb(err);

        return cb(null, res);
      });
    });
  }

  /**
   * Send Broadcast Messages
   *
   * @method sendMultiple
   * @param {Object} data
   * @param {Function} cb
   * @return {Object}
  */
  static broadcast(userIds, conversationIds, messageData, cb) {
    if (!MessageService.Client || !MessageService.Client.getInstance()) {
      return cb('You have not initialized channelize client yet.');
    }
    const client = MessageService.Client.getInstance();

    return promiseOrCallback(cb, (cb) => {
      userIds = userIds || [];
      conversationIds = conversationIds || [];

      if (!userIds.length && !conversationIds.length) {
        Logger.log(ERROR, errors.REQUIRED_USER_OR_CONVERSATION, {});
        return cb(errors.REQUIRED_USER_OR_CONVERSATION);
      }

      if (typeof messageData !== 'object') {
        Logger.log(ERROR, errors.INVALID_MULTIPLE_MESSAGE_OBJECT, {});
        return cb(errors.INVALID_MULTIPLE_MESSAGE_OBJECT);
      }

      this._validateMessageObject(messageData);

      const data = {};
      if (userIds.length) {
        data.userIds = userIds;
      } else if (conversationIds.length) {
        data.conversationIds = conversationIds;
      }

      Object.assign(data, messageData);

      // Request Channelize API
      client.request(`${MessageService.basePath}/broadcast`, { method: 'post', data }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: client.error(err) });
        return cb(client.error(err));
      });
    });
  }

  /**
   * Delete Messages
   *
   * @method deleteMessages
   * @param {Array} messageIds
   * @param {Function} cb
   * @return {Boolean}
  */
  static deleteMessagesForMe(messageIds, cb) {
    if (!MessageService.Client || !MessageService.Client.getInstance()) {
      return cb('You have not initialized channelize client yet.');
    }
    const client = MessageService.Client.getInstance();

    return promiseOrCallback(cb, (cb) => {
      // Check Params Validation
      if (!Array.isArray(messageIds)) {
        Logger.log(ERROR, errors.INVALID_MESSAGE_IDS, {});
        return cb(errors.INVALID_MESSAGE_IDS);
      }

      // Request Channelize Api
      client.request(`${MessageService.basePath}/delete`, { method: 'post', data: { messageIds } }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: client.error(err) });
        return cb(client.error(err));
      });
    });
  }

  /**
   * Delete Messages For Everyone
   *
   * @method deleteForEveryone
   * @param {Array} messageIds
   * @param {Function} cb
   * @return {Array}
  */
  static deleteMessagesForEveryone(messageIds, cb) {
    if (!MessageService.Client || !MessageService.Client.getInstance()) {
      return cb('You have not initialized channelize client yet.');
    }
    const client = MessageService.Client.getInstance();

    return promiseOrCallback(cb, (cb) => {
      // Check Params Validation
      if (!Array.isArray(messageIds)) {
        Logger.log(ERROR, errors.INVALID_MESSAGE_IDS, {});
        return cb(errors.INVALID_MESSAGE_IDS);
      }

      // Request Channelize Api
      client.request(`${MessageService.basePath}/delete_for_everyone`, { method: 'post', data: { messageIds } }).then((res) => cb(null, res.data)).catch((err) => {
        Logger.log(ERROR, errors.API_ERROR, { err: client.error(err) });
        return cb(client.error(err));
      });
    });
  }

  static _sendMessage(data, cb) {
    if (!MessageService.Client || !MessageService.Client.getInstance()) {
      return cb('You have not initialized channelize client yet.');
    }
    const client = MessageService.Client.getInstance();

    this._validateMessageObject(data);

    // Request Channelize Api
    client.request(`${MessageModel.basePath}/send`, { method: 'post', data }).then((res) => cb(null, new MessageModel(client, res.data))).catch((err) => {
      Logger.log(ERROR, errors.API_ERROR, { err: client.error(err) });
      return cb(client.error(err));
    });
  }

  static _validateMessageObject(data) {
    if (data.body && typeof data.body !== 'string') {
      throw new Error(errors.INVALID_MESSAGE_BODY);
    }

    if (data.attachments && !Array.isArray(data.attachments)) {
      throw new Error(errors.INVALID_ATTACHMENT_TYPE);
    }

    if (!data.body && !data.attachments) {
      throw new Error(errors.REQUIRED_BODY_OR_ATTACHMENT);
    }

    if (data.parentId && typeof data.parentId !== 'string') {
      throw new Error(errors.INVALID_MESSAGE_PARENTID);
    }

    if (data.customType && typeof data.customType !== 'string') {
      throw new Error(errors.INVALID_MESSAGE_CUSTOMTYPE);
    }

    if (data.showInConversation && typeof data.showInConversation !== 'boolean') {
      throw new Error(errors.INVALID_MESSAGE_SHOWINCONVERSATION);
    }

    if (data.recipients && !Array.isArray(data.recipients)) {
      throw new Error(errors.INVALID_MESSAGE_RECIPIENTS);
    }

    if (data.events && typeof data.events !== 'object') {
      throw new Error(errors.INVALID_MESSAGE_EVENTS);
    }

    const allowedTypes = ['normal', 'reply', 'admin'];
    if (data.type && !allowedTypes.includes(data.type)) {
      throw new Error(errors.INVALID_MESSAGE_TYPE);
    }

    if (data.attachments && data.attachments.length) {
      data.attachments.forEach((attachment) => {
        if (!attachment.type) {
          throw new Error(errors.REQUIRED_ATTACHMENT_TYPE);
        }
      });
    }

    if (data.isGroup && data.mentionedUsers) {
      if (!Array.isArray(data.mentionedUsers)) {
        throw new Error(errors.INVALID_MESSAGE_MENTIONED_USERS);
      }

      for (let i = data.mentionedUsers.length - 1; i >= 0; i -= 1) {
        const requiredFilters = Utils.getRequiedFilter(['userId', 'order', 'wordCount'], data.mentionedUsers[i]);
        if (requiredFilters.length) {
          throw new Error(errors.INVALID_MESSAGE_MENTIONED_USERS);
        }
      }
    }

    if (data.parentId && !data.type) {
      data.type = 'reply';
    } else {
      data.type = data.type ? data.type : 'normal';
    }
  }
}
